<template>
  <div class="product-detail">
    <a-steps :current="current" class="product-steps">
      <a-step v-for="item in steps" :key="item.title" :title="item.title" />
    </a-steps>
    <div class="steps-content">
      <basicInfo class="basicDetail"
      :form="basicForm"
      :options="options"
      :optionsObj="optionsObj"
      v-show="current === 0"
      @next="next"
      ></basicInfo>
      <saleInfo
      :form="saleForm"
      class="saleDetail"
      v-show="current === 1"
      @prev="prev"
      @submitAdd="submitAdd"
      ></saleInfo>
    </div>
  </div>
</template>
<script>
import basicInfo from '@/components/basicDetail/index.vue';

import saleInfo from '@/components/saleDetail/index.vue';

export default {
  components: {
    basicInfo,
    saleInfo,
  },
  data() {
    return {
      options: [],
      optionsObj: {},
      basicForm: {
        title: '',
        desc: '',
        category: null,
        c_items: [],
        tags: [],
        c_item: '',
      },
      saleForm: {
        price: null,
        price_off: null,
        unit: '',
        status: 1,
        images: [],
        inventory: null,
      },
      current: 0,
      steps: [
        {
          title: '填写商品基本信息',
        },
        {
          title: '填写商品销售信息',
        },
      ],
    };
  },
  computed: {
    form() {
      return {
        ...this.basicForm,
        ...this.saleForm,
      };
    },
  },
  async created() {
    const optionData = await this.$api.catlist();
    this.options = optionData.data.data;
    this.options.forEach((data) => {
      this.optionsObj[data.id] = data.c_items;
    });
  },
  methods: {
    next(form) {
      this.current += 1;
      this.basicForm = {
        ...form,
        c_items: [form.c_item],
      };
    },
    prev() {
      this.current -= 1;
    },
    async submitAdd(form) {
      this.saleForm = {
        ...form,
      };
      try {
        await this.$api.add(this.form);
        this.$message.success('添加成功');
        this.$router.push({ name: 'ProductList' });
      } catch (error) {
        this.$message.error(error);
      }
    },
  },
};
</script>
<style scoped lang='less'>
.product-detail{
.product-steps{
  width:50%;
  margin: 20px auto;
}

.steps-content {
  margin-top: 16px;
  border: 1px dashed #e9e9e9;
  border-radius: 6px;
  background-color: #fafafa;
  min-height: 200px;
  text-align: center;
  padding-top: 40px;
}
}

</style>
